<template>

  <div :class="shopRowClass" :style="{height:mHeight+'px'}">
    <shop-select :state="state" @shop-select-click="selectClick"></shop-select>
    <div class="shop-cart-row-content">
      <div v-for="(item,index) in info">
        {{item}}
      </div>
    </div>
    <span>编辑</span>
  </div>

</template>

<script type="text/ecmascript-6">
  import shopSelect from '../ShopCartSelect/ShopCartSelect.vue';
  export default{
    components: {
      shopSelect
    },
    props: {
      state: {
        type: Boolean,
        default: false
      },
      info: {
        type: Array,
        default: []
      },
      position: {
        type: Number,
        default: -1
      },
      mHeight: {
        type: Number,
        default: 60
      }
    },
    computed: {
      shopRowClass: function () {
        return this.state ? 'shop-cart-row-active' : 'shop-cart-row'
      }
    },
    data(){
      return {}
    },
    created: function () {
      //  实例创建完成后调用，此阶段完成了数据的观测等，但尚未挂载，还不可用。需要初始化处理一些数据时会比较有用。

    },
    mounted(){
      //  el挂载到实例上后会调用，第一个业务逻辑一般从这里开始。
    },
    beforeDestroy: function () {
      //  实例销毁之前调用。主要是解绑一些使用addEventListener监听的事件等。
    },
    methods: {
      selectClick(){
        let _this = this;
        _this.$emit('shop-row-select', {
          index: _this.position
        })
      }
    }

  }
</script>
<style scoped lang="scss">
  .shop-cart-row {
    display: flex;
    padding: 0 16px;
    justify-content: space-between;
    align-items: center;
    margin: -1px auto;
    background: whitesmoke;
    color: darkgray;
    border-top: solid darkgray 1px;
    border-bottom: solid darkgray 1px;

    &-content {
      width: 80%;
      text-align: left;
    }
  }

  .shop-cart-row-active {
    display: flex;
    padding: 0 16px;
    justify-content: space-between;
    align-items: center;
    margin: -1px auto;
    background: white;
    color: black;
    border-top: solid darkgray 1px;
    border-bottom: solid darkgray 1px;
    &-content {
      width: 80%;
      text-align: left;
    }
  }
</style>



