<template>

  <div class="shoes-card-content" :style="shoesCardStyle" @click="handleShoesClick($event)">
    <div class="shoes-card-txt" v-show="showInfo">
      <span :class="shoesCardTxtClass('name')">{{shoes.name}}</span>
      <span :class="shoesCardTxtClass('theme')">{{shoes.theme}}</span>
    </div>
    <div :class="shoes.price!=='通知我√'?'shoes-card-button':'shoes-card-button notify'" v-show="showInfo">{{shoes.price}}
    </div>
    <i class="iconfont icon-share" v-show="showShare"></i>
    <i class="iconfont icon-xin" v-show="showPick"></i>
    <img class="shoes-card-img" v-lazy="shoes.img">
  </div>

</template>

<script type="text/ecmascript-6">
  export default{
    components: {},
    props: {
      shoes: {
        type: Object,
        default: {
          id: 1,
          name: '鞋子名字',
          theme: '鞋子主题',
          color: 0,
          price: 0,
          img: ''
        }
      },
      shoesSize: {
        type: Number,
        default: 100
      },
      showInfo: {
        type: Boolean,
        default: true
      },
      showShare: {
        type: Boolean,
        default: false
      },
      showPick: {
        type: Boolean,
        default: false
      }
    },
    computed: {
      shoesCardStyle: function () {
        let size = this.shoesSize + 'px';
        let style = {
          width: size,
          height: size
        };
        return style
      },
    },
    data(){
      return {}
    },
    created: function () {
      //  实例创建完成后调用，此阶段完成了数据的观测等，但尚未挂载，还不可用。需要初始化处理一些数据时会比较有用。

    },
    mounted(){
      //  el挂载到实例上后会调用，第一个业务逻辑一般从这里开始。
    },
    beforeDestroy: function () {
      //  实例销毁之前调用。主要是解绑一些使用addEventListener监听的事件等。
    },
    methods: {
      shoesCardTxtClass(className){
        return this.shoes.color === 0 ? 'shoes-card-txt-' + className + ' txt-black' : 'shoes-card-txt-' + className + ' txt-white'
      },
      clickShoesButton(price){
        let _this = this;
        let data = {
          target: _this.shoes.id,
          shoes: _this.shoes
        };
        if (price === '通知我') {
          data['state'] = 0;//  点击 设定通知
        } else if (price === '通知我√') {
          return;
        } else {
          data['state'] = 1;//点击 购买
        }
        this.$emit('shoes-button-click', data);
      },
      handleShoesClick(event){
        let _this = this;
        let target = event.target;
        if (target.className === 'shoes-card-button') {
          _this.clickShoesButton(_this.shoes.price);
        } else {
          let obj = {
            target: _this.shoes.id
          };
          this.$emit('shoes-click', obj);
        }
      }
    }

  }
</script>
<style scoped lang="scss">
  $colors: black, white;
  @mixin txtColor($Colors) {
    @for $i from 1 through length($Colors) {
      $color: nth($Colors, $i);
      .txt-#{$color} {
        color: $color;
      }
    }
  }

  @include txtColor($colors)

  .shoes-card-content {
    position: relative;
    margin-top: 2px;
    margin-right: 2px;
    transition: all .2s;
  }

  .shoes-card-txt {
    position: absolute;
    left: 0;
    top: 0;
    transform: translate(12px, 18px);
    text-align: left;
    &-name {
      display: block;
      font-size: 14px;
    }
    &-theme {
      font-size: 24px;
      font-weight: bold;
    }
  }

  .shoes-card-img {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 100%;
    z-index: -1;
  }

  .shoes-card-button {
    width: 80px;
    line-height: 40px;
    height: 40px;
    border: solid 1px black;
    border-radius: 4px;
    position: absolute;
    left: 0;
    bottom: 0;
    font-size: 18px;
    transform: translate(20%, -50%);
  }

  .notify {
    border: solid 1px lightgray;
    color: lightgray;
  }

  .iconfont {
    position: absolute;
    right: 0;
    bottom: 0;
    font-size: 30px;
    font-weight: bold;
  }

  .icon-share {
    transform: translate(-250%, -50%);
  }

  .icon-xin {
    transform: translate(-50%, -50%);
  }
</style>



