export default {
  purchase: [
    {
      user_id: 'u_1',
      shoes_id: '1'
    },
    {
      user_id: 'u_1',
      shoes_id: '2'
    },
    {
      user_id: 'u_1',
      shoes_id: '3'
    },
    {
      user_id: 'u_1',
      shoes_id: '4'
    }
  ],
  purchasePick: [
    {
      user_id: 'u_1',
      shoes_id: '6'
    },
    {
      user_id: 'u_1',
      shoes_id: '7'
    },
    {
      user_id: 'u_1',
      shoes_id: '9'
    },
  ],
  addPurchase: function (data) {
    this.purchase.push({
      user_id: data.user_id,
      shoes_id: data.shoes.id
    })
  },
  addPurchasePick: function (data) {
    this.purchasePick.push({
      user_id: data.user_id,
      shoes_id: data.shoes.id
    })
  }
}



