<template>
  <div id="app" @touchstart="handleTouchStart($event)" @touchmove="handleTouchMove($event)"
       @touchend="handleTouchEnd($event)">
    <div v-if="deviceType===1">
      <router-view/>
      <transition name="menu-bottom">
        <menu-bottom :menus="menusArray" @menu-bottom-click="handleMenuBottom" v-show="showMenuBottom"></menu-bottom>
      </transition>
      <transition name="icon-fangdajing">
        <i class="iconfont icon-fangdajing" v-show="showMenuBottom"></i>
      </transition>
      <transition name="shoesDetail">
        <shoes-detail :shoes="shoesDetail" v-if="showShoesDetail"
                      @close-shoes-detail="handleCloseShoesDetail"></shoes-detail>
      </transition>
      <shop-cart @hidden-shop-cart="hiddenShopCart" v-show="showShopCart"></shop-cart>
    </div>
    <app-pc v-if="deviceType===0"></app-pc>
  </div>
</template>

<script>
  import AppPc from './AppPc.vue';
  export default {
    components: {AppPc},
    name: 'App',
    component: {
      AppPc
    },
    data(){
      return {
        touchData: {
          startPos: {},
          movePos: {},
          endPos: {},
          touchDirection: 0,
        },
        touchUsableEl: [
          'shoes-card-content',
          'shoes-card-list-content-right',
          'shoes-card-list-content-left',
          'shoes-card-list-content-left-img',
          'shoes-latest-item',
          'shoes-latest-item-left',
          'shoes-latest-item-left-img',
          'shoes-latest-item-right',
          ''
        ],
        deviceType: -1
      }
    },
    computed: {
      showMenuBottom: function () {
        return this.$store.state.moduleMenu.show;
      },
      menusArray: function () {
        return this.$store.state.moduleMenu.menuItems
      },
      showShopCart: function () {
        return this.$store.state.moduleShopCart.show;
      },
      shoesData: function () {
        return this.$store.state.moduleShoes.shoesData;
      },
      showShoesDetail: function () {
        return this.$store.state.moduleShoes.shoesDetailState;
      },
      shoesDetail: function () {
        return this.$store.state.moduleShoes.shoesDetail;
      }
    },
    mounted(){
      //  el挂载到实例上后会调用，第一个业务逻辑一般从这里开始。
      let _this = this;
      (function IsPC() {
        let userAgentInfo = navigator.userAgent;
        let Agents = ["Android", "iPhone",
          "SymbianOS", "Windows Phone",
          "iPad", "iPod"];
        let flag = true;
        for (let v = 0; v < Agents.length; v++) {
          if (userAgentInfo.indexOf(Agents[v]) > 0) {
            flag = false;
            break;
          }
        }
        _this.deviceType = flag ? 0 : 1
      })();
    },
    methods: {
      handleMenuBottom(data){
        let _this = this;
        let target = data.target;
        _this.$store.dispatch({
          type: 'moduleMenu/changeMenuPosition',
          target: target,
          context: _this,
          callBack: function (context) {
            context.$router.push(data.route);
          }
        });
      },
      handleCloseShoesDetail(){
        let _this = this;
        _this.$store.commit({
          type: 'moduleShoes/shoesDetailShowOrClose'
        });
      },
      hiddenShopCart(){
        let _this = this;
        _this.$store.commit({
          type: 'moduleShopCart/clearShopCart'
        });
      },
      findElFromArr(name){
        for (let i = 0, length = this.touchUsableEl.length; i < length; i++) {
          if (name === this.touchUsableEl[i]) {
            return true
          }
        }
        return false;
      },
//      移动端触摸事件
      handleTouchStart(e){
        if (this.showShoesDetail) {
//          e.preventDefault();
          return;
        }
        if (this.findElFromArr(e.target.className)) {
          let _this = this;
          let touchData = _this.touchData;
          let touch = e.targetTouches[0];
          touchData.startPos = {x: touch.pageX, y: touch.pageY, time: +new Date}; //取第一个touch的坐标值
          touchData.touchDirection = 0;
          this.$store.commit({
            type: 'moduleMenu/MenuShowOrHidden',
            show: false
          })
        } else {
//          e.preventDefault();
        }
      },
      handleTouchMove(e){
        if (this.showShoesDetail) {
//          e.preventDefault();
          return;
        }
        if (this.findElFromArr(e.target.className)) {
          let _this = this;
          let touchData = _this.touchData;

          //当屏幕有多个touch或者页面被缩放过，就不执行move操作
          if (e.targetTouches.length > 1 || e.scale && e.scale !== 1) return;
          let touch = e.targetTouches[0];
          touchData.movePos = {
            x: touch.pageX - touchData.startPos.x,
            y: touch.pageY - touchData.startPos.y
          };
          touchData.endPos = {
            x: touch.pageX,
            y: touch.pageY
          };
          touchData.touchDirection = Math.abs(touchData.movePos.x) < Math.abs(touchData.movePos.y) ? 1 : 0;
          //touchDirection为1时，表示纵向滑动，0为横向滑动
          if (touchData.touchDirection === 0) {
            console.log('横向滚动');
            e.preventDefault();
          } else {
            console.log('纵向滚动');
          }
        } else {
//          e.preventDefault();
        }
      },
      handleTouchEnd(e){
        if (this.showShoesDetail) {
//          e.preventDefault();
          return;
        }

        if (this.findElFromArr(e.target.className)) {
          let _this = this;
          let touchData = _this.touchData;
          let duration = +new Date - touchData.startPos.time; //滑动的持续时间
          if (touchData.touchDirection === 0) { //当为水平滚动时
            if (Number(duration) > 30) {
              //判断是左移还是右移，当偏移量大于10时执行
              console.log(touchData.startPos.x);
              console.log(touchData.endPos.x);
              let offsetX = (touchData.endPos.x - touchData.startPos.x).toFixed(2);
              if (offsetX < -130) {
                _this.$bus.$emit('change-to-right');
              } else if (offsetX > 130) {
                _this.$bus.$emit('change-to-left');
              }
            }
          } else {
            //垂直滚动

          }
          _this.$store.commit({
            type: 'moduleMenu/MenuShowOrHidden',
            show: true
          });
          _this.touchData = {
            startPos: {},
            movePos: {},
            endPos: {},
            touchDirection: 0,
          }
        } else {
//          e.preventDefault();
        }
      }
    }
  };
</script>

<style lang="scss">
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    position: relative;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
  }

  .menu-bottom {
    &-enter-active, &-leave-active {
      transition: all .3s;
    }
    &-enter, &-leave-to {
      opacity: 0;
      transform: translateY(60px);
    }
    &-enter-to, &-leave {
      opacity: 1;
    }
  }

  .icon-fangdajing {
    width: 60px;
    height: 60px;
    line-height: 60px;
    position: fixed;
    right: 0;
    bottom: 0;
    transform: translate(-50%, -150%);
    border-radius: 50%;
    background: white;
    box-shadow: 0px 4px 4px 0px darkgrey;

    &-enter-active, &-leave-active {
      transition: all .3s;
    }
    &-enter, &-leave-to {
      opacity: 0;
      transform: translate(-50%, -150%) scale(0);
    }
    &-enter-to, &-leave {
      opacity: 1;
      transform: translate(-50%, -150%) scale(1);
    }
  }

  .shoesDetail-enter-active, .shoesDetail-leave-active {
    transition: all .4s;
  }

  .shoesDetail-enter {
    opacity: 0;
    transform: scale(0);
  }

  .shoesDetail-enter-to {
    opacity: 1;
    transform: scale(1);
  }

  .shoesDetail-leave {
    opacity: 1;
    left: 0;
  }

  .shoesDetail-leave-to {
    opacity: 0;
    left: -375px !important;
  }

  .font-bold {
    font-weight: bold;
  }

  .color-lightgray {
    color: lightgray;
  }

  .color-darkgrey {
    color: darkgrey;
  }

  $sizes: 15, 16, 18, 20, 22, 24, 28;
  @mixin fontSize($sizes) {
    @for $i from 1 through length($sizes) {
      $size: nth($sizes, $i);
      .font-size-#{$size} {
        font-size: $size + px !important;
      }
    }
  }

  @include fontSize($sizes)

</style>



