<template>

  <div class="shoes-scroll-content" :style="scrollContentStyle" @touchstart="handleTouchStart($event)"
       @touchmove="handleTouchMove($event)" @touchend="handleTouchEnd($event)">
    <div class="shoes-scroll-items" :style="scrollContentItemStyle">
      <div v-for="(item,index) in items" :key="index" class="shoes-scroll-image">
        <img :src="item.url">
      </div>
    </div>

    <span class="shoes-scroll-name">{{info.name}}</span>
    <span class="shoes-scroll-theme">{{info.theme}}</span>
    <div class="shoes-scroll-button" @click="handleButtonClick">{{info.state}}</div>
    <ul class="scroll-point">
      <li class="point" v-for="(item,index) in items" :key="index"></li>
    </ul>
    <li class="pointActive" :style="pointActiveStyle"></li>
  </div>

</template>

<script type="text/ecmascript-6">
  export default{
    components: {},
    props: {
      basicSize: {
        type: Object,
        default: {
          width: 100,
          height: 100
        }
      },
      items: {
        type: Array,
        default: []
      },
      info: {
        type: Object,
        default: {}
      }
    },
    computed: {
      scrollContentStyle: function () {
        let _this = this;
        let style = {
          width: _this.basicSize.width + 'px',
          height: _this.basicSize.height + 225 + 'px'
        };
        return style
      },
      scrollContentItemStyle: function () {
        let _this = this;
        let style = {
          width: _this.basicSize.width * this.items.length + 'px',
          height: _this.basicSize.height + 'px',
          transform: 'translateX(' + _this.offsetX + 'px)'
        };
        return style
      },
      pointActiveStyle: function () {
        let length = this.items.length;
        let pos = this.position;
        let left = (length - 1 - pos) * 16 + (length - pos) * 8;
        left = -(left + 20);
        let style = {
            transform: 'translate(' + left + 'px,-20px)'
          }
        ;
        return style;
      }
    },
    data(){
      return {
        position: 0,
        touchData: {
          startPos: {},
          movePos: {
            x: 0,
          },
          endPos: {},
          touchDirection: 0,
        },
        offsetX: 0
      }
    },
    created: function () {
      //  实例创建完成后调用，此阶段完成了数据的观测等，但尚未挂载，还不可用。需要初始化处理一些数据时会比较有用。

    },
    mounted(){
      //  el挂载到实例上后会调用，第一个业务逻辑一般从这里开始。
    },
    beforeDestroy: function () {
      //  实例销毁之前调用。主要是解绑一些使用addEventListener监听的事件等。
    },
    methods: {
      handleButtonClick(){
        let _this = this;
        _this.$store.commit({
          type: 'moduleShopCart/openShopCart',
          shoes: {
            id: _this.info.id,
            name: _this.info.name,
            theme: _this.info.theme
          }
        })
      },
      handleTouchStart(e){
        let _this = this;
        let touchData = _this.touchData;
        let touch = e.targetTouches[0];
        touchData.startPos = {x: touch.pageX, y: touch.pageY, time: +new Date}; //取第一个touch的坐标值
        touchData.endPos = {
          x: touchData.startPos.x,
          y: touchData.startPos.y
        };
      },
      handleTouchMove(e){
        let _this = this;
        let touchData = _this.touchData;
        let touch = e.targetTouches[0];
        //当屏幕有多个touch或者页面被缩放过，就不执行move操作
        if (e.targetTouches.length > 1 || e.scale && e.scale !== 1) return;
        touchData.movePos = {
          x: touch.pageX - touchData.endPos.x,
          y: touch.pageY - touchData.endPos.y
        };
        touchData.endPos = {
          x: touch.pageX,
          y: touch.pageY
        };
        touchData.touchDirection = Math.abs(touchData.movePos.x) < Math.abs(touchData.movePos.y) ? 1 : 0;
        //touchDirection为1时，表示纵向滑动，0为横向滑动
        if (touchData.touchDirection === 0) {
          e.preventDefault();
          _this.offsetX += touchData.movePos.x;
          if (touchData.movePos.x > 0) {
            //  左-》右
          } else {
            //  右-》左
          }
        } else {
//          e.preventDefault();
        }
      },
      handleTouchEnd(e){
        let _this = this;
        let touchData = _this.touchData;

        let pos = _this.position;
        let x = touchData.endPos.x - touchData.startPos.x;
        if (x < 0) {
          if (x < -130) {
            if (pos === (_this.items.length - 1)) {
              pos = 0;
            } else {
              pos++;
            }
          } else {
          }
          _this.offsetX = -375 * pos;
        } else {
          if (x > 130) {
            if (pos === 0) {
              pos = _this.items.length - 1;
            } else {
              pos--;
            }
          } else {

          }
          _this.offsetX = -375 * pos;
        }
        _this.position = pos;
      }
    }

  }
</script>
<style scoped lang="scss">
  $className: 'shoes-scroll';
  .#{$className}-content {
    width: 100%;
    position: relative;
    background: whitesmoke;
  }

  .#{$className}-items {
    display: flex;
    position: absolute;
    top: 160px;
    left: 0;
    transition: all .2s;
  }

  .#{$className}-image {
    width: 100%;
    height: 100%;
    & img {
      width: 100%;
      height: 100%;
    }
  }

  .#{$className}-name {
    position: absolute;
    left: 0;
    top: 0;
    transform: translate(20px, 20px);
    font-size: 20px;
    font-weight: bold;
    color: black;
    height: 16px;
    line-height: 16px;
  }

  .#{$className}-theme {
    position: absolute;
    left: 0;
    top: 0;
    transform: translate(20px, 40px);
    font-size: 26px;
    font-weight: bold;
    color: black;
    height: 24px;
    line-height: 24px;
  }

  .#{$className}-button {
    position: absolute;
    left: 0;
    bottom: 0;
    transform: translate(20px, -20px);
    font-size: 18px;
    background: whitesmoke;
    color: black;
    border: solid 1px black;
    width: 108px;
    height: 40px;
    line-height: 40px;
  }

  .scroll-point {
    position: absolute;
    right: 0;
    bottom: 0;
    height: 8px;
    line-height: 8px;
    transform: translate(-20px, -20px);
    justify-content: space-around;
    align-items: center;
    margin: 0;
    .point {
      width: 8px;
      height: 8px;
      margin: 0 8px;
      background: darkgray;
      border-radius: 50%;
      list-style: none;
      display: inline-block;
    }
  }

  .pointActive {
    position: absolute;
    width: 8px;
    height: 8px;
    right: 0;
    bottom: 0;
    background: black;
    list-style: none;
    border-radius: 50%;
    /*transition: all .1s;*/
  }
</style>



