<template>

  <div class="shop-cart-content">
    <div class="shop-cart-shadow" @click="hiddenShopCart">

    </div>
    <div class="shop-cart-body">
      <div class="shop-cart-info" v-show="position===0">
        <div class="shop-cart-info-head">
          <span>{{shoes.name}}</span>
          <div>{{shoes.theme}}</div>
        </div>
        <div class="shop-cart-info-row" v-for="(item,index) in infoList" @click="clickInfo(item.name)">
          <span>{{item.nameCN}}</span>
          <div>{{item.value}}</div>
          <i>+</i>
        </div>
        <div class="shop-cart-info-footer">
          <span>点击立即购买即表示您同意Nike的<a>销售条款</a>。</span>
          <div>2018年09月05日起无理由退换货为14天(详见退换货政策)</div>
        </div>

        <shop-button :state="infoFull" :buttonStyle="btnSizeStyle" :txt="'立即购买'"
                     @shop-button-click=""></shop-button>
      </div>

      <div class="shop-cart-size" v-show="position === 1">
        <div class="shop-cart-size-head" @click="clickInfo('back')">
          <span>尺码</span>
          <i>-</i>
        </div>
        <div class="shop-cart-size-body">
          <div class="shop-cart-size-body-grid" v-for="(item,index) in sizeList" @click="handleGridClick(index)"
               :style="getGridStyle(item.state,index)">
            {{item.size}}
          </div>
        </div>
        <div class="shop-cart-size-foot">
          <shop-button :state="itemSelected !== -1" :buttonStyle="btnSizeStyle" :txt="'确定'"
                       @shop-button-click="selectedSize"></shop-button>
        </div>
      </div>

      <div class="shop-cart-address" v-show="position === 2">
        <div class="shop-cart-size-head" @click="clickInfo('back')">
          <span>地址和运费</span>
          <i>-</i>
        </div>

        <shop-row v-for="(item,index) in addressList" :key="index"
                  :state="item.state" :info="[item.name,item.address,item.city]" :position="index"
                  :mHeight="80"
                  @shop-row-select="handleAddressSelectClick">
        </shop-row>
        <shop-button :state="false" :buttonStyle="btnAddressStyle" :txt="'新地址'"
                     @shop-button-click=""></shop-button>
        <shop-row v-for="(item,index) in expressList" :key="item.id"
                  :state="item.state" :info="[item.name+' '+item.time+' '+item.price]" :position="index"
                  @shop-row-select="handleExpressSelectClick">
        </shop-row>
        <shop-button :state="true" :buttonStyle="btnSizeStyle" :txt="'继续'"
                     @shop-button-click="clickInfo('back')"></shop-button>
      </div>
    </div>
  </div>

</template>

<script type="text/ecmascript-6">
  import shopButton from './ShopCartButton/ShopCartButton.vue';
  import shopSelect from './ShopCartSelect/ShopCartSelect.vue';
  import ShopRow from './ShopCartRow/ShopCartRow.vue';

  export default{
    components: {
      shopButton, shopSelect, ShopRow
    },
    props: {},
    computed: {
      shoes: function () {
        return this.$store.state.moduleShopCart.shoes;
      },
      infoList: function () {
        return this.$store.getters['moduleShopCart/infoList'];
      },
      sizeList: function () {
        return this.$store.state.moduleShopCart.sizeList;
      },
      addressList: function () {
        return this.$store.state.moduleShopCart.addressList;
      },
      expressList: function () {
        return this.$store.state.moduleShopCart.expressList;
      },
      payList: function () {
        return this.$store.state.moduleShopCart.payList;
      },
      btnSizeStyle: function () {
        return this.$store.state.moduleShopCart.btnSizeStyle;
      },
      btnAddressStyle: function () {
        return this.$store.state.moduleShopCart.btnAddressStyle;
      },
      position: function () {
        return this.$store.state.moduleShopCart.position;
      },
      itemSelected: function () {
        return this.$store.state.moduleShopCart.itemSelected;
      },
      infoFull: function () {
        return this.$store.state.moduleShopCart.infoFull;
      },
      shopCartButtonClass: function () {
        return this.itemSelected === -1 ? 'shop-cart-button' : 'shop-cart-button-active'
      }
    },
    data(){
      return {}
    },
    created: function () {
      //  实例创建完成后调用，此阶段完成了数据的观测等，但尚未挂载，还不可用。需要初始化处理一些数据时会比较有用。

    }

    ,
    mounted()
    {
      //  el挂载到实例上后会调用，第一个业务逻辑一般从这里开始。
    }
    ,
    beforeDestroy: function () {
      //  实例销毁之前调用。主要是解绑一些使用addEventListener监听的事件等。
    }
    ,
    methods: {
      getGridStyle(state, index)
      {
        let style = {};
        if (state === -1) {
          style['background'] = 'whitesmoke';
          style['color'] = 'darkgrey';
        } else {
          if (this.itemSelected === index) {
            style['background'] = 'black';
            style['color'] = 'white';
          } else {
            style['background'] = 'white';
            style['color'] = 'black';
          }
        }
        return style;
      }
      ,
      handleGridClick(index)
      {
        this.$store.commit({
          type: 'moduleShopCart/updateItemSelected',
          _index: index
        })
      }
      ,
      selectedSize(){
        let _this = this;
        if (this.itemSelected === -1) return;
        this.$store.commit({
          type: 'moduleShopCart/updateInfoListSize',
        });
        this.clickInfo('back');
      }
      ,
      handleAddressSelectClick(data){
        this.$store.commit({
          type: 'moduleShopCart/updateAddressList',
          _index: data.index
        })
      },
      handleExpressSelectClick(data){
        this.$store.commit({
          type: 'moduleShopCart/updateExpressList',
          _index: data.index
        })
      },
      clickInfo(name)
      {
        if (name === 'size') {
          this.$store.commit({
            type: 'moduleShopCart/updatePosition',
            _position: 1
          })
        } else if (name === 'address') {
          this.$store.commit({
            type: 'moduleShopCart/updatePosition',
            _position: 2
          })
        } else if (name === 'pay') {
          this.$store.commit({
            type: 'moduleShopCart/updatePosition',
            _position: 3
          })
        } else if (name === 'invoice') {
          this.$store.commit({
            type: 'moduleShopCart/updatePosition',
            _position: 4
          })
        } else if (name === 'sum') {
          this.$store.commit({
            type: 'moduleShopCart/updatePosition',
            _position: 5
          })
        } else if (name === 'back') {
          this.$store.commit({
            type: 'moduleShopCart/updatePosition',
            _position: 0
          })
        }
      },
      hiddenShopCart(){
        this.$emit('hidden-shop-cart');
      }
    }

  }
</script>
<style scoped lang="scss">
  $className: 'shop-cart';
  .#{$className}-content {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 11;
    transition: all .5s;
  }

  .#{$className}-shadow {
    background: lightgrey;
    opacity: .7;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
  }

  .#{$className}-body {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: white;
    opacity: 1;
  }

  .#{$className}-info {

    &-row {
      margin: -1px 16px 0 16px;
      height: 50px;
      border-top: solid 1px lightgrey;
      border-bottom: solid 1px lightgrey;
      display: flex;
      justify-content: space-around;
      align-items: center;

      & span {
        width: 20%;
        font-weight: bold;
        font-size: 13px;
        text-align: left;
      }
      & div {
        width: 70%;
        color: darkgray;
      }
      & i {
        width: 10%;
        color: black;
      }

    }

    &-head {
      padding: 10%;
      text-align: center;
      & span {
        font-size: 26px;
        font-weight: bold;
        color: black;
      }
      & div {
        font-size: 16px;
        font-weight: bold;
        color: gray;
      }
    }

    &-footer {
      width: 80%;
      margin-top: 10%;
      margin-left: 10%;
      text-align: center;
      & span {
        color: darkgray;
        font-size: 14px;
      }
      & div {
        color: black;
        font-size: 14px;
      }
    }
  }

  .#{$className}-size {

    &-head {
      padding: 16px;
      height: 16px;
      font-weight: bold;
      border-bottom: solid lightgray 1px;
      & span {
        float: left;
      }
      & i {
        float: right;
      }
    }
    &-body {
      padding: 0 16px 0 16px;
      text-align: left;
      border-top: solid lightgray 1px;
      &-grid {
        width: 68.6px;
        height: 68.6px;
        line-height: 68.6px;
        text-align: center;
        display: inline-block;
        border: solid lightgray 1px;
        margin: -1px;
      }
    }
    &-footer {

    }
  }

  .#{$className}-address {

  }

  .#{$className}-button {
    padding: 8px 16px;
    margin: 16px;
    background: lightgray;
    color: white;
    font-size: 18px;
    font-weight: bold;
  }

  .#{$className}-button-white {
    padding: 16px;
    margin: 16px;
    background: white;
    border: solid 1px black;
    color: black;
    font-size: 18px;
    font-weight: bold;
  }

  .#{$className}-button-active {
    padding: 16px;
    margin: 16px;
    font-size: 18px;
    font-weight: bold;
    background: black;
    color: white;
  }
</style>



