// 引入mockjs
const Mock = require('mockjs');
// 获取 mock.Random 对象
const Random = Mock.Random;

import shoesData from './data/shoesData';
import purchaseData from './data/purchaseData';

Random.extend({
  shoes: function (shoes_id) {
    let _id = shoes_id;
    const shoesArray = shoesData.shoesArray;
    let data;
    if (_id === '-1') {
      //  要全部鞋子
      data = [];
      data = shoesArray;
    } else {
      data = {};
      for (let i = 0, length = shoesArray.length; i < length; i++) {
        if (shoesArray[i].id === _id) {
          data = shoesArray[i];
          return data
        }
      }
    }
    // return this.pick(shoesArray)
    return data;
  },
  shoesImages: function (id) {
    if (id === '-1') {
      return [];
    }
    const imageArray = shoesData.imageArray;
    let _id = id;
    let data = [];
    for (let i = 0, length = imageArray.length; i < length; i++) {
      if (imageArray[i].shoes_id === _id) {
        data.push(imageArray[i]);
      }
    }
    return data;
  },
  userPurchase: function (user_id, type) {
    let purchaseArray = type === '0' ? purchaseData.purchasePick : purchaseData.purchase;
    let purchase = [];
    for (let i = 0, length = purchaseArray.length; i < length; i++) {
      if (purchaseArray[i].user_id === user_id) {
        purchase.push(purchaseArray[i]);
      }
    }
    return purchase;
  }
});

// Mock.mock( url, post/get , 返回的数据)；
// Mock.mock('/getShoes', 'get', products(-1));
let mockInfo = {
  "code": 404,
};
Mock.mock(RegExp('' + ".*"), 'get', (options) => {
  let url = options.url;
  let indexOfBreak = url.indexOf('?');
  let api = url.substring(0, url.indexOf('?'));
  let params = url.substring(url.indexOf('?') + 1);
  params = params.split('&');
  if (api === '/getShoes') {
    let param = params[0].split('=')[0];
    let value = params[0].split('=')[1];
    if (param === 's_id') {
      let shoesResult = Random.shoes(value);
      let imagesResult = Random.shoesImages(value);
      if (shoesResult.length < 1) {
        mockInfo['code'] = -1;
      } else {
        mockInfo['code'] = 0;
        mockInfo['result'] = {
          shoes: shoesResult,
          images: imagesResult
        };
      }
      return Mock.mock(mockInfo);
    }
  } else if (api === '/getPurchase') {
    let user_id = '';
    let type = -1;
    for (let i = 0; i < params.length; i++) {
      let param = params[i].split('=')[0];
      let value = params[i].split('=')[1];
      if (param === 'user_id') {
        user_id = value;
      } else if (param === 'type') {
        type = value;
      }
    }
    if (user_id !== '' && type !== -1) {
      //查询对应订单
      let purchaseResult = Random.userPurchase(user_id, type);
      for (let i = 0; i < purchaseResult.length; i++) {
        let shoesResult = Random.shoes(purchaseResult[i].shoes_id);

        if (JSON.stringify(shoesResult) !== '{}') {
          purchaseResult[i]['shoes'] = shoesResult;
        } else {
          purchaseResult[i]['shoes'] = {
            id: '-9999',
            name: '',
            theme: '',
            color: 1,
            price: '通知我',
            time: '1993-07-25 09:00',
            state: 0, //0--精选  1--热卖  3--待售
            img: '',
            content: '',
            detail: []
          };
        }
      }
      mockInfo['code'] = 0;
      mockInfo['result'] = {
        purchase: purchaseResult,
      };
      return Mock.mock(mockInfo);

    } else {
      console.log('error');
    }
  }
});



