<template>

  <div class="shoes-detail-content">
    <div class="shoes-detail-head">
      <div @click="clickButtonBack">
        <i class="iconfont icon-arrow-left-s-line"></i>
      </div>
      <div>
        <span class="font-bold font-size-22">{{shoes.basic.name}}</span>
      </div>
    </div>
    <div v-if="shoes.basic.type === 0 ||imagesThumbnail.length<1">
      <shoes-card :shoes="shoes.basic" :shoesSize="shoesSize" :showInfo="true"
                  @shoes-button-click="handleShoesCardButtonClick"></shoes-card>
    </div>
    <div class="shoes-detail-body-thumbnail" v-if="imagesThumbnail.length>1 && shoes.basic.type === 1">
      <shoes-scroll :basicSize="{width:375,height:375}"
                    :items="imagesThumbnail"
                    :info="{name:shoes.basic.name,theme:shoes.basic.theme,state:shoes.basic.price}"></shoes-scroll>
    </div>

    <div class="shoes-detail-body">
      <div class="shoes-detail-body-info">
        <div class="color-darkgrey font-size-16">{{shoes.basic.name}}</div>
        <div class="font-bold font-size-22">{{shoes.basic.theme}}</div>
        <div class="font-bold font-size-16">{{shoes.basic.price}}</div>
        <div class="font-bold font-size-16">预计{{shoes.basic.time}}开售</div>
        <div class="color-darkgrey font-size-16 shoes-detail-body-info-content">{{shoes.basic.content}}</div>
        <div class="color-darkgrey font-size-16">一旦您提交订单即表明您同意下属条款</div>
        <div class="shoes-detail-body-info-option">
          <i class="iconfont icon-share"></i>
          <i class="iconfont icon-xin"></i>
        </div>
      </div>
      <div class="shoes-detail-body-thumbnail" v-if="imagesThumbnail.length>1 && shoes.basic.type===0">
        <shoes-scroll :basicSize="{width:375,height:375}"
                      :items="imagesThumbnail"
                      :info="{name:shoes.basic.name,theme:shoes.basic.theme,state:shoes.basic.price}"></shoes-scroll>
      </div>
      <div class="shoes-detail-body-attention">
        <div class="font-size-15 color-darkgrey" v-for="(item,index) in shoes.basic.detail" :key="index">
          {{item}}
        </div>
      </div>
      <div class="shoes-detail-body-detail">
        <div v-for="(item,index) in imagesDetail" :key="index">
          <img :src="item.url">
        </div>
      </div>
    </div>
    <div class="shoes-detail-footer">
      <span class="color-darkgrey">发布于{{shoes.basic.time}}</span>
      <div v-for="(item,index) in shoesLabel">{{item}}</div>
    </div>
    <div class="shoes-detail-btnBack">
      <i class="iconfont icon-yooxi" @click="clickButtonBack"></i>
    </div>
  </div>

</template>

<script type="text/ecmascript-6">
  import shoesCard from '../../components/ShoesCard/ShoesCard.vue';
  import shoesScroll from '../../components/shoesScroll/shoesScroll.vue';

  export default{
    components: {
      shoesCard, shoesScroll
    },
    props: {
      shoes: {
        type: Object,
        default: {
          basic: {
            name: '',
            theme: 1,
            price: 0,
            time: '',
            content: '',
            detail: '',
          },
          images: []
        }
      }
    },
    computed: {
      imagesDetail: function () {
        let arr = this.shoes.images;
        let images = [];
        for (let i = 0; i < arr.length; i++) {
          if (arr[i].type === 'details') {
            images.push(arr[i]);
          }
        }
        return images
      },
      imagesThumbnail: function () {
        let arr = this.shoes.images;
        let images = [];
        for (let i = 0; i < arr.length; i++) {
          if (arr[i].type === 'thumbnail') {
            images.push(arr[i]);
          }
        }
        return images
      },
      shoesLabel: function () {
        let label = this.shoes.basic.label;
        if (typeof(label) === "undefined") {
          return []
        }
        label = label.split(' ');
        let i = 0;
        while (i < label.length) {
          while (label[i].indexOf('-') !== -1) {
            label[i] = label[i].replace('-', ' ');
          }
          i++;
        }

        return label;
      }
    },
    data(){
      return {
        shoesSize: document.documentElement.clientWidth
      }
    },
    created: function () {
      //  实例创建完成后调用，此阶段完成了数据的观测等，但尚未挂载，还不可用。需要初始化处理一些数据时会比较有用。

    },
    mounted(){
      //  el挂载到实例上后会调用，第一个业务逻辑一般从这里开始。
      console.log(this.shoes);
    },
    beforeDestroy: function () {
      //  实例销毁之前调用。主要是解绑一些使用addEventListener监听的事件等。
    },
    methods: {
      clickButtonBack(){
        this.$emit('close-shoes-detail');
      },
      handleShoesCardButtonClick(data){
        let _this = this;
        console.log(data);
        if (data.state === 1) {
          //购买
          _this.$store.commit({
            type: 'moduleShopCart/openShopCart',
            shoes: {
              id: data.shoes.id,
              name: data.shoes.name,
              theme: data.shoes.theme
            }
          })
        }
      }
    }

  }
</script>
<style scoped lang="scss">
  $className: 'shoes-detail';
  .#{$className}-content {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 10;
    overflow-y: auto;
    background: white;
    overflow-x: hidden;
  }

  .#{$className}-head {
    background: whitesmoke;
    text-align: left;
    height: 60px;
    display: flex;
    align-items: center;
    box-shadow: 1px 1px 4px 4px lightgray;

    & div {
      height: 100%;
      line-height: 60px;
    }
    & div:first-child {
      width: 15%;
    }
    & div:last-child {
      width: 80%;
    }
  }

  .#{$className}-body {
    &-info {
      padding: 10%;
      overflow: hidden;
      &-content {
        margin-top: 16px;
        line-height: 32px;
      }
      &-option {
        position: relative;
        width: 30%;
        left: 50%;
        margin-top: 20px;
        transform: translateX(-50%);
        & .iconfont {
          font-size: 30px;
        }
      }
    }
    &-thumbnail {
      overflow-x: auto;
    }
    &-attention {
      padding: 10%;
      & div {
        margin-top: 20px;
        line-height: 30px;
      }
    }
    &-detail {
      position: relative;
      & img {
        width: 100%;
      }
    }
  }

  .#{$className}-footer {
    width: 100%;
    min-height: 40px;
    background: whitesmoke;
    position: relative;
    padding: 20px 0;
    & span {
      display: block;
      margin: 20px auto;
    }
    & div {
      padding: 4px 10px;
      display: inline-block;
      margin: 10px 10px;
      height: 38px;
      line-height: 38px;
      background: darkgrey;
      color: white;
    }
  }

  .#{$className}-btnBack {
    position: fixed;
    right: 0;
    bottom: 0;
    transform: translate(-50%, -50%);

    .icon-yooxi {
      position: absolute;
      right: 0;
      bottom: 0;
      font-size: 40px;
      animation: iconShow 1s ease-out infinite alternate;
    }

    @keyframes iconShow {
      0% {
        opacity: 0;
        transform: translate(-50%, -50%);
      }
      100% {
        opacity: 1;
        transform: translate(-70%, -50%);
      }
    }
  }

  .icon-arrow-left-s-line {
    font-size: 28px;
    margin-left: 6px;
  }

  .icon-xin {
    float: right;
  }

  .icon-share {
    float: left;;
  }
</style>



