// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import App from './App';

import router from './router';

//axios use
import axios from 'axios'
import VueAxios from 'vue-axios'
Vue.use(VueAxios, axios);

//vuex use

import store from './store';

Vue.config.devtools = true;
Vue.config.productionTip = false;

import menuBottom from './components/MenuBottom/MenuBottom.vue';
import shoesDetail from './components/ShoesDetail/ShoesDetail.vue';
import shopCart from './components/ShopCart/ShopCart.vue';

Vue.component('menu-bottom', menuBottom);
Vue.component('shoes-detail', shoesDetail);
Vue.component('shop-cart', shopCart);

import VueLazyLoad from 'vue-lazyload'
Vue.use(VueLazyLoad, {
  error: require('./assets/error.jpeg'),
  loading: require('./assets/loading.gif')
});

let bus = new Vue();
Vue.prototype.$bus = bus;

require('./extends/mock.js');

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,//使用store
  components: {App},
  template: '<App/>',
});



