const state = {
  menuItems: [
    {
      id: 1,
      txt: '主页',
      img: 'icon-home',
      active: true,
      route: 'home'
    },
    {
      id: 2,
      txt: '发现',
      img: 'icon-zhinanzhen',
      active: false,
      route: 'test'
    },
    {
      id: 3,
      txt: '收件箱',
      img: 'icon-youxiang',
      active: false,
      route: 'test'
    },
    {
      id: 4,
      txt: '档案',
      img: 'icon-fl-renyuan',
      active: false,
      route: 'archives'
    }

  ],
  show: true
};
const getters = {
  position: function (state) {
    for (let i = 0, length = state.menuItems.length; i < length; i++) {
      if (state.menuItems[i].active) {
        return i
      }
    }
    return 0
  }
};
const mutations = {
  MenuShowOrHidden(state, payload){
    state.show = payload.show;
  },
  updateShoesItems(state, payload){
    let _target = payload.target;
    for (let i = 0, length = state.menuItems.length; i < length; i++) {
      state.menuItems[i].active = state.menuItems[i].id === _target;
    }
  }
};
const actions = {
  changeMenuPosition({state, getters, rootState, rootGetters, commit, dispatch}, payload){
    let _target = payload.target;
    let context = payload.context;
    commit({
      type: 'updateShoesItems',
      target: _target
    });
    payload.callBack(context);
  }
};


const _moduleMenu = {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
};
export default _moduleMenu



