const state = {
  //  所有球鞋数据 Start
  shoesData: [],
  // End
  shoesDetail: {},
  //  球鞋详情弹框 Start
  shoesDetailState: false,  //false - 关闭 true - 打开
  // End
};
const getters = {
  shoesDataFormat: function (state, getters, rootState, rootGetters) {
  }
};
const mutations = {
  shoesDetailShowOrClose(state, getters, rootState, rootGetters){
    state.shoesDetailState = !state.shoesDetailState;
  },
  //球鞋查询结果更新 Start
  shoesSelectSuccess(state, payload){
    state.shoesData = payload.shoes;
  },
  shoesSelectEmpty(state, payload){
    state.shoesData = payload.shoes;
  },
  updateShoesDetail(state, payload){
    state.shoesDetail = payload.shoesDetail;
  }
  //End
};
const actions = {
  getAllShoes({state, getters, rootState, rootGetters, commit, dispatch}, payload){
    let _context = payload.context;
    _context.axios.get('/getShoes?s_id=-1').then((response) => {
      if (response.data.code === 0) {
        let _data = response.data.result.shoes;
        commit({
          type: 'shoesSelectSuccess',
          shoes: _data
        });
        _data = null;
      } else {
        commit({
          type: 'shoesSelectEmpty',
          shoes: []
        })
      }
    });
  }
};

const _moduleShoes = {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
};
export default _moduleShoes



