<template>

  <div class="shop-cart-select" @click="iconClick">
    <i :class="iconClass"></i>
  </div>

</template>

<script type="text/ecmascript-6">
  export default{
    components: {},
    props: {
      state: {
        type: Boolean,
        default: false
      }
    },
    computed: {
      iconClass: function () {
        return this.state ? 'iconfont icon-check' : 'iconfont icon-yuancircle46';
      }
    },
    data(){
      return {}
    },
    created: function () {
      //  实例创建完成后调用，此阶段完成了数据的观测等，但尚未挂载，还不可用。需要初始化处理一些数据时会比较有用。

    },
    mounted(){
      //  el挂载到实例上后会调用，第一个业务逻辑一般从这里开始。
    },
    beforeDestroy: function () {
      //  实例销毁之前调用。主要是解绑一些使用addEventListener监听的事件等。
    },
    methods: {
      iconClick(){
        this.$emit('shop-select-click');
      }
    }

  }
</script>
<style scoped lang="scss">
  .shop-cart-select {
    & i {
      font-size: 20px;
    }
  }
</style>



