<template>

  <div class="shop-cart-button" :style="getButtonStyle" @click="btnClick">
    {{txt}}
  </div>

</template>

<script type="text/ecmascript-6">
  export default{
    components: {},
    props: {
      state: {
        type: Boolean,
        default: false
      },
      buttonStyle: {
        type: Object,
        default: {
          background: 'white',
          backgroundActive: 'black',
          color: 'black',
          colorActive: 'white',
          padding: {
            top: 8, bottom: 8, left: 16, right: 16
          },
        }
      },
      txt: {
        type: String,
        default: '按钮'
      }
    },
    computed: {
      getButtonStyle: function () {
        let _this = this;
        let style = {
          background: _this.state ? _this.buttonStyle.backgroundActive : _this.buttonStyle.background,
          color: _this.state ? _this.buttonStyle.colorActive : _this.buttonStyle.color,
          border: _this.state ? 'solid 1px ' + _this.buttonStyle.colorActive : 'solid 1px ' + _this.buttonStyle.color,
          padding: _this.buttonStyle.padding.top + 'px ' + _this.buttonStyle.padding.left + 'px '
          + _this.buttonStyle.padding.bottom + 'px ' + _this.buttonStyle.padding.right + 'px'
        };
        return style
      }
    },
    data(){
      return {}
    },
    created: function () {
      //  实例创建完成后调用，此阶段完成了数据的观测等，但尚未挂载，还不可用。需要初始化处理一些数据时会比较有用。

    },
    mounted(){
      //  el挂载到实例上后会调用，第一个业务逻辑一般从这里开始。
    },
    beforeDestroy: function () {
      //  实例销毁之前调用。主要是解绑一些使用addEventListener监听的事件等。
    },
    methods: {
      btnClick(){
        this.$emit('shop-button-click');
      }
    }

  }
</script>
<style scoped>
  .shop-cart-button {
    margin: 16px;
    font-size: 18px;
    font-weight: bold;
  }
</style>



