import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);

// 由于 store 中的状态是响应式的，在组件中调用 store 中的状态简单到仅需要在计算属性中返回即可。
// 触发变化也仅仅是在组件的 methods 中提交 mutation。

// 球鞋模块
// 对于模块内部的 mutation 和 getter，接收的第一个参数是模块的局部状态对象。
// 对于模块内部的 action，局部状态通过 context.state 暴露出来，根节点状态则为 context.rootState：
// 对于模块内部的 getter，根节点状态会作为第三个参数暴露出来：
// 默认情况下，模块内部的 action、mutation 和 getter 是注册在全局命名空间的——这样使得多个模块能够对同一 mutation 或 action 作出响应。
// 如果希望你的模块具有更高的封装度和复用性，你可以通过添加 namespaced: true 的方式使其成为带命名空间的模块。
// 当模块被注册后，它的所有 getter、action 及 mutation 都会自动根据模块注册的路径调整命名。
import _moduleShoes from './module/shoes';
import _moduleMenu from './module/menu';
import _moduleShopCart from './module/shopCart';

const store = new Vuex.Store({
  strict: true,
  state: {},
  getters: {
    // getTodoById: (state) => (id) => {
    //   return state.todos.find(todo => todo.id === id)
    // }
  },
  // 你可以向 store.commit 传入额外的参数，即 mutation 的 载荷（payload）：
  // 在大多数情况下，载荷应该是一个对象，这样可以包含多个字段并且记录的 mutation 会更易读：
  // 提交 mutation 的另一种方式是直接使用包含 type 属性的对象：
  //                                                    store.commit({
  //                                                        type: 'increment',
  //                                                        amount: 10 })
  /*
   既然 Vuex 的 store 中的状态是响应式的，那么当我们变更状态时，监视状态的 Vue 组件也会自动更新。
   这也意味着 Vuex 中的 mutation 也需要与使用 Vue 一样遵守一些注意事项：
   1.最好提前在你的 store 中初始化好所有所需属性。
   2.当需要在对象上添加新属性时，你应该
   使用 Vue.set(obj, 'newProp', 123), 或者以新对象替换老对象。
   例如，利用 stage-3 的对象展开运算符我们可以这样写：
   state.obj = { ...state.obj, newProp: 123 }

   mutation 必须是同步函数
   */
  mutations: {},
  /*
   actions:
   * // 以载荷形式分发
   store.dispatch('incrementAsync', {
   amount: 10
   })
   // 以对象形式分发
   store.dispatch({
   type: 'incrementAsync',
   amount: 10
   })

   store.dispatch 可以处理被触发的 action 的处理函数返回的 Promise，并且 store.dispatch 仍旧返回 Promise：

   一个 store.dispatch 在不同模块中可以触发多个 action 函数。在这种情况下，只有当所有触发函数完成后，返回的 Promise 才会执行。
   * */
  actions: {},
  modules: {
    moduleShoes: _moduleShoes,
    moduleMenu: _moduleMenu,
    moduleShopCart: _moduleShopCart
  }
});

export default store;



